import {  Badge } from "react-bootstrap";
import PageTitle from "../layouts/PageTitle";
import Moment from "react-moment";

import { useEffect } from "react";
// CONSTANTS
import { SERVICE } from "../../../_constants";

import TableGenerator from "../components/tableGenerator.component";

import { useTranslation } from "react-i18next";
import useServiceContextHook from "../../../_hooks/service.context.hook";
import numeral from "numeral";

function Deposits() {
  const { t } = useTranslation();
  const {
    services: { transaction },
    useService,
  } = useServiceContextHook();

  let service = useService({
    [SERVICE?.FIND]: transaction.find,
  });
  const { dispatchRequest } = service;

  useEffect(() => {
    dispatchRequest({
      type: SERVICE?.FIND,
      payload: {
        where: {
          type: {
            $or: ["DEPOSIT", "CREDIT"],
          },
        },
        // fake: true,
        sudo: true,
      },
    });
  }, []);

  return (
    <>
      <PageTitle activeMenu="Deposits" motherMenu="Wallet management" />
      <header className="mb-4">
        <h3>{t("Deposits List")}</h3>
      </header>

      <div style={{ marginBottom: 60 }}>
        <DepositsHistoryTable {...{ service }} />
      </div>
    </>
  );
}
function DepositsHistoryTable({ service }) {
  return (
    <>
      <TableGenerator
        {...{ service }}
        mapping={{
          id: "USER ID",
          createdAt: "joined",
        }}
        omit="*"
        extras={["id", "email", "type", "currency", "amount", "status", "date", ]}
        transformers={{
          id: ({ row }) => row?.id,
          email: ({ row }) => row?.wallet?.user?.email,
          type: ({ row }) => (
            <div className="text-capitalize">
              {String(row?.type)?.toLowerCase()}
            </div>
          ),
          currency: ({ row }) => row?.wallet?.currency,
          amount: ({ row }) => String(row?.quantity),
          status: ({ row }) => (
            <Badge
              pill
              variant={((status) => {
                switch (status) {
                  case "active":
                  case "completed": {
                    return "success";
                  }
                  case "declined": {
                    return "danger";
                  }
                  default: {
                    return "info";
                  }
                }
              })(String(row?.status)?.toLowerCase())}
              className="text-white"
            >
              {String(row?.status)?.toLowerCase()}
            </Badge>
          ),
          date: ({ row }) => {
            return <Moment format="MMM Do Y" date={row?.createdAt} />;
          },
        }}
      />
    </>
  );
}

export default Deposits;
